'use client'

import { Award, Star } from 'lucide-react';

export function InstructorsSection() {
  // Hoofdtrainer
  const headInstructor = {
    name: 'Rachid Ousllam',
    role: 'Hoofdtrainer & Mentaal Begeleider',
    rank: '3e Dan Zwarte Band',
    experience: 'Sinds 2013',
    image: '/gallery/rachid.jpeg',
    bio: 'Rijksgediplomeerd Taekwondo Trainer Niveau 3 | Gecertificeerd Coach met Continentale Licentie. Erkend door Taekwondobond Nederland (TBN) en NOC*NSF. Oprichter van Taekwondo Black Dragon Den Haag (2013) en houder van de 3e Dan in Taekwondo, officieel erkend door World Taekwondo (WT) en TBN.',
    achievements: [
      'Mentale begeleiding: versterkt zelfvertrouwen en veerkracht',
      'Persoonlijke aanpak: trainingen afgestemd op niveau en doelen',
      'Ervaring met alle leeftijden: van kinderen tot volwassenen',
      'Focus op discipline en respect: persoonlijke groei naast techniek',
      'Competitie & recreatie: plezier Ã©n prestatie in sport',
    ],
  };

  // Assistent trainers (2x2 grid)
  const assistantInstructors = [
    {
      name: 'Ouiam Ousllam',
      role: 'Assistent Trainer',
      image: '/placeholder.svg',
      bio: 'Toegewijde assistent trainer die leerlingen helpt hun vaardigheden te ontwikkelen.',
    },
    {
      name: 'Isra Jallab',
      role: 'Assistent Trainster',
      image: '/instructors/wissal-ousllam.jpg',
      bio: 'Gepassioneerde assistent trainster met uitgebreide ervaring in het lesgeven aan kinderen en jongeren.',
    },
    {
      name: 'Wissal Ousllam',
      role: 'Assistent Trainster',
      image: '/instructors/isra-jallab.jpg',
      bio: 'Enthousiaste assistent trainster die zich richt op het ontwikkelen van technische vaardigheden en het opbouwen van zelfvertrouwen bij leerlingen.',
    },
    {
      name: 'Nando Silva',
      role: 'Assistent Trainer',
      image: '/placeholder.svg',
      bio: 'Enthousiaste assistent trainer met passie voor het onderwijzen van Taekwondo.',
    },
  ];

  return (
    <section id="instructors" className="py-16 bg-muted/30">
      <div className="container mx-auto px-4 max-w-7xl">
        
        {/* Header */}
        <div className="mb-12">
          <div className="max-w-3xl space-y-4">
            <div className="inline-block">
              <span className="text-primary font-bold text-xs uppercase tracking-[0.2em]">
                {'Instructeurs'}
              </span>
            </div>
            <h2 className="font-serif text-4xl sm:text-5xl md:text-6xl font-bold text-foreground leading-[1.1] tracking-tight">
              {'Ons'}<br />
              <span className="text-primary">{'Team'}</span>
            </h2>
            <div className="w-20 h-1 bg-primary"></div>
            <div className="space-y-4 max-w-2xl">
              <p className="text-foreground text-lg sm:text-xl leading-[1.5] font-normal">
                Ervaring. Expertise. Passie.
              </p>
            </div>
          </div>
        </div>

        {/* Hoofdtrainer - Compacter */}
        <div className="mb-16">
          <div className="grid lg:grid-cols-2 gap-8 items-center max-w-5xl mx-auto">
            {/* Photo */}
            <div className="max-w-md mx-auto lg:mx-0">
              <div className="relative aspect-[4/5] overflow-hidden rounded-lg">
                <img
                  src={headInstructor.image}
                  alt={headInstructor.name}
                  className="w-full h-full object-cover"
                />
                <div className="absolute inset-0 bg-gradient-to-t from-black/80 via-black/20 to-transparent" />
                
                {/* Rank Badge */}
                <div className="absolute top-6 right-6 bg-primary text-primary-foreground px-4 py-2 font-bold text-sm">
                  {headInstructor.rank}
                </div>

                {/* Name Overlay */}
                <div className="absolute bottom-6 left-6 right-6">
                  <h3 className="font-serif text-3xl sm:text-4xl font-bold text-white mb-2">{headInstructor.name}</h3>
                  <p className="text-primary text-base font-semibold">{headInstructor.role}</p>
                </div>
              </div>
            </div>

            {/* Content */}
            <div className="space-y-6">
              {/* Experience */}
              <div className="flex items-center gap-4">
                <Award className="w-7 h-7 text-primary flex-shrink-0" />
                <span className="text-xl font-bold text-foreground">{headInstructor.experience} {'ervaring'}</span>
              </div>

              {/* Bio */}
              <p className="text-foreground/80 text-lg leading-[1.8]">
                {headInstructor.bio}
              </p>

              {/* Achievements */}
              <div>
                <h4 className="text-base font-bold text-foreground mb-4">
                  {'Specialisaties'}
                </h4>
                <div className="space-y-3">
                  {headInstructor.achievements.map((achievement, i) => (
                    <div key={i} className="flex items-start gap-4">
                      <Star className="w-5 h-5 text-primary flex-shrink-0 mt-0.5" />
                      <span className="text-foreground/80 text-base leading-[1.7]">{achievement}</span>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Assistent Trainers - 2x2 Grid */}
        <div>
          <h3 className="font-serif text-2xl sm:text-3xl font-bold text-foreground mb-8 text-center">
            Assistent Trainers
          </h3>
          <div className="grid sm:grid-cols-2 lg:grid-cols-4 gap-6 max-w-6xl mx-auto">
            {assistantInstructors.map((instructor, index) => (
              <div key={index} className="bg-card rounded-lg overflow-hidden group hover:shadow-lg transition-all duration-300">
                {/* Photo */}
                <div className="relative aspect-[3/4] overflow-hidden">
                  <img
                    src={instructor.image}
                    alt={instructor.name}
                    className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-500"
                  />
                  <div className="absolute inset-0 bg-gradient-to-t from-black/80 via-black/20 to-transparent" />

                  {/* Name Overlay */}
                  <div className="absolute bottom-4 left-4 right-4">
                    <h4 className="font-serif text-xl font-bold text-white mb-1">{instructor.name}</h4>
                    <p className="text-primary text-sm font-semibold">{instructor.role}</p>
                  </div>
                </div>

                {/* Content */}
                <div className="p-4">
                  <p className="text-foreground/70 text-sm leading-relaxed">
                    {instructor.bio}
                  </p>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
}
